
.Instrcutor-container{
    .backgroundPicture{
        background: url("../../../public/images/Profile\ Background.png") center/cover;
        height: 50vh;

    }
    // padding: 0 2rem;
    .instName{
        display:flex ;
        
        h3{
            margin:0.2rem 0;
        }
        a{
            text-decoration: none;
        }
        p{

            margin:0.2rem 0;
        }
        // margin-right: 6rem;
    }

    .grids{
        display: grid;
        grid-template-columns: repeat(3,1fr);
        .span-1{
            grid-column:1/4;
            @media (min-width:900px){
                grid-column:1/3;
                grid-row:1/2;
            }
        }
        .span-2{
            grid-column:1/4;
            @media(min-width:768px){
                grid-column:3/4;
                grid-row:5/6;
            }
            @media(min-width:900px){
                grid-column:3/4;
                grid-row:1/3;
            }
            .MuiImageList-root{
                width:80%;
                @media (min-width:768px){
                    width:300px;
                    height:300px;
                }
                @media (min-width:1024px){
                    width:400px;
                    height:400px;
                }
            }
        }
        .span-3{
            grid-column:1/4;
            @media(min-width:768px){
                grid-column:1/4;
                grid-row:2/3;
            }
            @media(min-width:900px){
                grid-column:1/3;
                grid-row:2/3;
            }
            @media(min-width:1024px){
                grid-column:1/3;
                grid-row:2/3;
            }
        }
        .span-4{
            grid-column:1/4;
            @media(min-width:768px){
                grid-column:1/3;
                grid-row:5/6;
            }
            @media(min-width:1024px){
                grid-column:3/4;
                grid-row:3/4;
            }
        }
        .span-5{
            grid-column:1/4;
            @media(min-width:768px){
                grid-row:3/4;
            }
            @media(min-width:1024px){
                grid-column:1/3;
                grid-row:3/4;
            }
        }
        .span-6{
            grid-column:1/4;
            padding: 0;
            h2{
                margin:0;   
            }
            @media(min-width:768px){
                grid-row:4/5;
            }
            @media(min-width:1024px){
                grid-column:1/3;
                grid-row:4/5;
            }
            
            
        }
    }
}

.span-6:hover img:not(:hover) {
    filter: blur(5px);   
}
.span-6{
    .MuiGrid-item{
        &:nth-child(2){
            .MuiGrid-item{
                transition: 0.2s ease-in-out;
                
                &:hover{
                    transform: scale(1.6);
                    cursor: pointer;
                    z-index: 10;
                }   
            }
            img{
                width: 60px;
                // transition: 0.2s ease-in-out;
                // &:hover{
                //     transform: scale(1.6);
                //     cursor: pointer;
                // }
            }
        }
    }
}