@import "../Utils/config.scss";
.navContainer {
  z-index:10;
  margin: 0 0;
  background-color: #252c3330;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  width:100%;
    a {
    text-decoration: none;
    color: $textColor1;
    text-transform: uppercase;
  }
  .logo{
      margin-left:2rem;
  }

  .logSign-Desktop {
    display: flex;
    align-items: center;
      margin-right:2rem;
    a {
      margin: 0 0.5rem;
      font-size: clamp(0.8rem,1vw,1.2rem);
    }
    button {
      border-radius: 1.2rem;
      background-color: $tertiaryColor;
      transition: 300ms ease-out;
      &:hover {
        background-color: $primaryColor;
        color: $textColor2;
      }
    }
    @media (max-width:900px){
      display: none;
    }
  }
}


#webapp_cover {
  z-index: 100;
  @media (min-width:900px){
    display: none;
  }
  transform: scale(1.2);
  margin-right: 2rem;
  border: none;
  background-color: transparent;
  #menu_button {
  width: 39px;
  overflow: hidden;
}

#menu_checkbox {
  display: none;
}

#menu_label {
  position: relative;
  display: block;
  height: 29px;
  cursor: pointer;
}

#menu_label:before,
#menu_label:after,
#menu_text_bar {
  position: absolute;
  left: 0;
  width: 100%;
  height: 5px;
  background-color: #fff;
}

#menu_label:before,
#menu_label:after {
  content: "";
  transition: 0.4s cubic-bezier(0.68, -0.55, 0.27, 1.55) left;
}

#menu_label:before {
  top: 0;
}

#menu_label:after {
  top: 12px;
}

#menu_text_bar {
  top: 24px;
}

#menu_text_bar:before {
  content: "MENU";
  position: absolute;
  top: 5px;
  right: 0;
  left: 0;
  color: #fff;
  font-size: 12px;
  font-weight: bold;
  font-family: "Montserrat", Arial, Helvetica, sans-serif;
  text-align: center;
}

#menu_checkbox:checked + #menu_label:before {
  left: -39px;
}

#menu_checkbox:checked + #menu_label:after {
  left: 39px;
}

#menu_checkbox:checked + #menu_label #menu_text_bar:before {
  animation: moveUpThenDown 0.8s ease 0.2s forwards,
    shakeWhileMovingUp 0.8s ease 0.2s forwards,
    shakeWhileMovingDown 0.2s ease 0.8s forwards;
  }
  
  @keyframes moveUpThenDown {
    0% {
    top: 0;
  }
  50% {
    top: -27px;
  }
  100% {
    top: -14px;
  }
}

@keyframes shakeWhileMovingUp {
  0% {
    transform: rotateZ(0);
  }
  25% {
    transform: rotateZ(-10deg);
  }
  50% {
    transform: rotateZ(0deg);
  }
  75% {
    transform: rotateZ(10deg);
  }
  100% {
    transform: rotateZ(0);
  }
}

@keyframes shakeWhileMovingDown {
  0% {
    transform: rotateZ(0);
  }
  80% {
    transform: rotateZ(3deg);
  }
  90% {
    transform: rotateZ(-3deg);
  }
  100% {
    transform: rotateZ(0);
  }
}

}



.menu {
  @media (max-width:900px){
    display: none;
  }
  display: flex;
  justify-content: space-between;
  position: relative;
  z-index: 1;
  max-width: 800px;
  
  @media (max-width: 650px) {
    flex-direction: column;
    align-items: center;
  }
  
  .item {
    padding: 0.5rem 1rem;
    cursor: pointer;
    transition: color .3s ease-out;
    text-decoration: none;
    color: #fff;
    text-transform: uppercase;
    font-size: clamp(0.8rem,0.5vw,1rem);
    
    
  }
  .active {
    color: #fff;
  }
  .indicator {
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
    border-radius: 30px;
  }
}
.mobile {
  background-color:rgba(136, 134, 138, 0.1098039216);

  a{
    margin: 1rem auto;
    svg{
      margin-top: 2rem;
      path{
        fill: #000;
      }
    }
    
}
  .menu-mobile {
    margin:2rem 2rem;
    display: flex;
    justify-content: space-between;
    position: relative;
    z-index: 1;
    max-width: 800px;
    @media (min-width: 900px) {
      display: none;

    }
    @media (max-width: 900px) {
      flex-direction: column;
      align-items: center;

    }
    
    .item-mobile {
      padding: 0.5rem 1rem;
      cursor: pointer;
      transition: color .3s ease-out;
      text-decoration: none;
      color: #000;
      text-transform: uppercase;
      font-size: clamp(0.8rem,1vw,1.2rem);
      
      
    }
    .active {
      color: #000;
    }
    .indicator-mobile {
      position: absolute;
      top: 0;
      left: 0;
      z-index: -1;
      border-radius: 30px;
    }
  } 
}